<template>
  <div>
    <!--   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <!--  -->
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrderList">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="pay_status" label="">
          <!--  -->
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="">
          <!--  -->
          <template slot-scope="scope">
            <template>{{scope.row.is_send}}</template>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="">
          <!--  -->
          <template slot-scope="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <!--  -->
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox(scope.row.order_id)"></el-button>

            <!--  -->
            <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10 , 15]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addressVisible"
    width="50%" @close="addressDialogClosed">
      <!--  -->
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="editOrderInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible"
    width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
  import cityData from './citydata.js'
export default {
  data(){
    return {
      // 
      queryInfo:{
        query:'',
        // 
        pagenum:1,
        // 
        pagesize:10
      },
      orderlist:[],
      total:0,
      addressVisible:false,
      addressForm:{
        address1:[],
        address2:''
      },
      addressFormRules:{
        address1:[
          {
            required:true,
            message:'',
            trigger:'blur'
          }
        ],
        address2:[
          {
            required:true,
            message:'',
            trigger:'blur'
          },
        ]
      },
      cityData,
      editForm:{},
      progressVisible:false,
      progressInfo:[]
    }
  },
  created(){
    this.getOrderList()
  },
  methods: {
    getOrderList(){
      this.$axios({
        methods:'get',
        url:'orders',
        params:this.queryInfo
      }).then(res=>{
        // console.log(res.data)
        if(res.data.meta.status !== 200)
          return this.$message.error('');
        this.orderlist = res.data.data.goods;
        this.total = res.data.data.total;

      }).catch(function(err){
        console.log(err);
      })
    },
    // pagesize
    handleSizeChange(newSize){
      this.queryInfo.pagesize=newSize;
      this.getOrderList();
    },
    // 
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage;
      this.getOrderList();
    },
    // 
    showBox(id){
      this.$axios.get('orders/' + id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error(res.data.meta.msg);
        }
        this.editForm = res.data.data;
        this.addressVisible=true;
      }).catch(err=>{
        console.log(err);
      })
    },
    // 
    addressDialogClosed(){
      this.$refs.addressFormRef.resetFields();
    },
    // 
    editOrderInfo(){
      this.$refs.addressFormRef.validate(valid=>{
        if(!valid) return;
        // 
        this.$axios.put('orders/'+this.editForm.order_id,this.editForm).then(res=>{
          if(res.data.meta.status !== 201){
            return this.$message.error(res.data.meta.msg);
          }
          // 
          this.addressVisible = false;
          // 
          this.getOrderList();
          // 
          this.$message.success(res.data.meta.msg)
        }).catch(err=>err)
      })
    },
    showProgressBox(){
      this.$axios.get('kuaidi/804909574412544580').then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.progressInfo = res.data.data;
        this.progressVisible = true
      }).catch(err=>{
        console.log(err);
      })

    }
  }
}
</script>
<style lang="less" scoped>
  .el-cascader{
    width: 100%;
  }
</style>



