<template>
  <div>
    <!--   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <tree-table class="el-table" :data="catelist" :columns="columns" :selection-type="false"
      :expand-type="false" show-index index-text="#" border
      :show-row-hover="false">
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen;"></i>
          <i class="el-icon-error" v-else style="color: red;"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag size="mini" type="success" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag size="mini" type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini"  @click="showEditDialog(scope.row.cat_id)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCateById(scope.row.cat_id)"></el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[3, 5, 10,15]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>

      <!--  -->
      <el-dialog title="" :visible.sync="addCateDialogVisible"
      width="50%" @close="addCateDialogClosed">
          <!--  -->
          <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
              <el-form-item label="" prop="cat_name">
                <el-input v-model="addCateForm.cat_name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-cascader :options="parentCateList"
                :props="cascaderProps"
                v-model="selectedKeys"
                @change="parentCateChanged"
                clearable props.checkStrictly>
                </el-cascader>
              </el-form-item>
          </el-form>
          <!--  -->
          <span slot="footer" class="dialog-footer">
            <el-button @click="addCateDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addCate"> </el-button>
          </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisible"
      width="50%" @close="editDialogClosed">
        <!--  -->
        <el-form :model="editForm" :rules="addCateFormRules" ref="editFormRef" label-width="80px">
          <el-form-item label="" >
            <el-input v-model="editForm.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editCateInfo"> </el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
  name: '',
  data(){
    return {
      // 
      queryInfo:{
        type:3,
        pagenum:1,
        pagesize:5
      },
      // 
      catelist:[],
      // 
      total:0,
      // table
      columns:[
        {
          label:'',
          prop:'cat_name'
        },
        {
          label:'',
          // 
          type:'template',
          // 
          template:'isok'
        },
        {
          label:'',
          // 
          type:'template',
          // 
          template:'order'
        },
        {
          label:'',
          // 
          type:'template',
          // 
          template:'opt'
        }
      ],
      // 
      addCateDialogVisible:false,
      // 
      addCateForm:{
        // 
        cat_name:'',
        // id
        cat_pid:0,
        // 
        cat_level:0
      },
      // 
      addCateFormRules:{
          cat_name:[
            {
              required:true,
              message:'',
              trigger:'blur'
            }
          ]
      },
      // 
      parentCateList:[],
      // 
      cascaderProps:{
        // checkStrictly:true,
        expandTrigger:'hover',
        value:'cat_id',
        label:'cat_name',
        children:'children'
      },
      // id
      selectedKeys:[],
      // 
      editDialogVisible:false,
      // 
      editForm:{},
    }
  },
  components: {},
  created(){
    this.getCateList()
  },
  mounted(){},
  methods: {
    // 
    getCateList(){
      this.$axios({
        methods:'get',
        url:'categories',
        params:this.queryInfo
      }).then(res=>{
        // console.log(res.data)
        if(res.data.meta.status !== 200)
          return this.$message.error('');
          // catelist
        this.catelist = res.data.data.result;
        // 
        this.total = res.data.data.total;

      }).catch(function(err){
        console.log(err);
      })
    },
    // pagesize
    handleSizeChange(newSize){
      this.queryInfo.pagesize=newSize;
      this.getCateList();
    },
    // 
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage;
      this.getCateList();
    },
    // 
    showAddCateDialog(){
      // 
      this.getParentCateList();
      // 
      this.addCateDialogVisible = true;
      // console.log('this.parentCateList')
    },
    // 
    getParentCateList(){
      this.$axios.get('categories',{
        params:{type:2}
      }).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success('');
        this.parentCateList = res.data.data;
        // console.log(this.cascaderProps)
      }).catch(err=>err)
    },
    // 
    parentCateChanged(){
      // selectedKeyslength0
      // 
      if(this.selectedKeys.length>0){
        // id
        this.addCateForm.cat_pid=this.selectedKeys[this.selectedKeys.length-1];
        // 
        this.addCateForm.cat_level = this.selectedKeys.length;
        return;
      }else{
        // id
        this.addCateForm.cat_pid= 0;
        // 
        this.addCateForm.cat_level = 0;
      }
    },
    // 
    addCate(){
      this.$refs.addCateFormRef.validate(valid=>{
        if(!valid) return
        this.$axios.post('categories',this.addCateForm).then(res=>{
          if(res.data.meta.status !== 201){
            return this.$message.error('');
          }
          this.$message.success('')
          this.getCateList();
          this.addCateDialogVisible = false;
        }).catch(err=>err)
      })
    },
    // 
    addCateDialogClosed(){
      this.$refs.addCateFormRef.resetFields();
      this.selectedKeys=[];
      this.addCateForm.cat_level = 0;
      this.addCateForm.cat_pid = 0;
    },
    // 
    showEditDialog(id){
      // console.log(id)
      this.$axios.get('categories/' + id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error(res.data.meta.msg);
        }
        this.editForm = res.data.data;
        this.editDialogVisible=true;
      }).catch(err=>{
        console.log(err);
      })
    },
    // 
    editDialogClosed(){
      this.$refs.editFormRef.resetFields();
    },
    // 
    editCateInfo(){
      this.$refs.editFormRef.validate(valid=>{
        if(!valid) return;
        // 
        this.$axios.put('categories/'+this.editForm.cat_id,{
          cat_name:this.editForm.cat_name
        }).then(res=>{
          if(res.data.meta.status !== 200){
            return this.$message.error('');
          }
          // 
          this.editDialogVisible = false;
          // 
          this.getCateList();
          // 
          this.$message.success('')
        })
      })
    },
    // id
    async removeCateById(id){
       // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      // confirm
      // cancel
      if(confirmResult != 'confirm'){
        return this.$message.info('');
      }
      this.$axios.delete('categories/'+id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success('');
        this.getCateList();
      }).catch(err=>err)
    }
  }
}
</script>
<style lang="less" scoped>
  .el-cascader{
  width: 100%;
}
</style>



