<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img src="../assets/image/login.jpg" alt="">
        <span></span>
      </div>
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <!--  -->
    <el-container>
      <!--  -->
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!--  -->
        <el-menu background-color="#333744" text-color="#fff"
        active-text-color="#409EFF" :unique-opened="true"
        :collapse="isCollapse" :collapse-transition="false"
        :router="true" :default-active="$route.path">
          <!--  -->
          <el-submenu :index="item.id+''" v-for="item in menulist" :key="item.id">
            <!--  -->
            <template slot="title">
              <!--  -->
              <i :class="iconsObj[item.id]"></i>
              <!--  -->
              <span>{{item.authName}}</span>
            </template>
            <!--  -->
            <el-menu-item :index="'/'+subItem.path" v-for="subItem in item.children" :key="subItem.id">
              <template slot="title">
                <!--  -->
                <i class="el-icon-menu"></i>
                <!--  -->
                <span>{{subItem.authName}}</span>
              </template>
            </el-menu-item>
          </el-submenu>

        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: '',
  data(){
    return {
      // 
      menulist:[],
      iconsObj:{
        '125':'iconfont icon-retractyonghuguanli',
        '103':'iconfont icon-retractquanxianguanli',
        '101':'iconfont icon-retractshangpinguanli',
        '102':'iconfont icon-retractdingdanguanli',
        '145':'iconfont icon-retracticon-'
      },
      // 
      isCollapse:false
    }

  },
  components: {},
  created(){
    this.getMenuList()
  },
  mounted(){},
  methods: {
    logout(){
      // token
      window.sessionStorage.clear();
      // 
      this.$router.push("/login");
    },
    // 
    getMenuList(){
      this.$axios({
        methods:'get',
        url:'menus'
      }).then(res=>{
        if(res.data.meta.status!=200)
          return this.$message.error(res.data.meta.msg);
        this.menulist = res.data.data;
      }).catch(function(err){
        console.log(err)
      })
    },
    // 
    toggleCollapse(){
      this.isCollapse = !this.isCollapse;
    }
  }
}
</script>
<style lang="less" scoped>
  @import "../assets/less/Home.less";
</style>



