<template>
  <div>
    <!--   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column prop="role_name" label=""></el-table-column>
        <el-table-column label="">
          <!--  -->
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="190px">
          <!--  -->
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
            </el-tooltip>

          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible"
    width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible"
    width="50%" @close="editDialogClosed">
      <!--  -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="" >
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="setRoleDialogVisible"
    width="50%" @close="setRoleDialogClosed">
      <!--  -->
      <div>
        <p>{{userInfo.username}}</p>
        <p>{{userInfo.role_name}}</p>
        <p>
          <el-select v-model="selectedRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id">
            </el-option>
          </el-select>
        </p>
      </div>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  data(){
    // 
    var checkEmail=(rule,value,cb)=>{
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      // 
      if(regEmail.test(value))
        return cb();
      cb(new Error(''));
    }
    // 
    var checkMobile=(rule,value,cb)=>{
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      // 
      if(regMobile.test(value))
        return cb();
      cb(new Error(''));
    }
    return {
      // 
      queryInfo:{
        query:'',
        // 
        pagenum:1,
        // 
        pagesize:2
      },
      userlist:[],
      total:0,
      // 
      addDialogVisible:false,
      // 
      addForm:{
        username:'',
        password:'',
        email:'',
        mobile:''
      },
      // 
      addFormRules:{
        username:[
          {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            min:3,
            max:10,
            message:'3~10',
            trigger:'blur'
          }
        ],
        password:[
          {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            min:6,
            max:15,
            message:'6~15',
            trigger:'blur'
          }
        ],
        email:[
        {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            validator:checkEmail,
            trigger:'blur'
          }
        ],
        mobile:[
          {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            validator:checkMobile,
            trigger:'blur'
          }
        ]
      },
      // 
      editDialogVisible:false,
      // 
      editForm:{},
      // 
      editFormRules:{
        email:[
        {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            validator:checkEmail,
            trigger:'blur'
          }
        ],
        mobile:[
          {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            validator:checkMobile,
            trigger:'blur'
          }
        ]
      },
      // 
      setRoleDialogVisible:false,
      // 
      userInfo:{},
      // 
      rolesList:[],
      // id
      selectedRoleId:''

    }
  },
  created(){
    this.getUserList()
  },
  methods: {
    getUserList(){
      this.$axios({
        methods:'get',
        url:'users',
        params:this.queryInfo
      }).then(res=>{
        // console.log(res.data)
        if(res.data.meta.status !== 200)
          return this.$message.error(res.data.meta.msg);
        this.userlist = res.data.data.users;
        this.total = res.data.data.total;

      }).catch(function(err){
        console.log(err);
      })
    },
    // pagesize
    handleSizeChange(newSize){
      this.queryInfo.pagesize=newSize;
      this.getUserList();
    },
    // 
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage;
      this.getUserList();
    },
    // switch
    userStateChanged(userinfo){
      var putData={
        'id':userinfo.id,
        'mg_state':userinfo.mg_state
      }
      this.$axios.put(`users/${userinfo.id}/state/${userinfo.mg_state}`,putData).then(res=>{
        // console.log(url)
        if(res.data.meta.status !== 200){
          userinfo.mg_state = !userinfo.mg_state;
          return this.$message.error(res.data.meta.msg);
        }
        this.$message.success(res.data.meta.msg);
      }).catch(function(err){
        console.log(err);
      })
    },
    // 
    addDialogClosed(){
      // 
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser(){
      // let userData = JSON.stringify(this.addForm);
      // console.log(userData)
      this.$refs.addFormRef.validate(valid=>{
        if(!valid) return;
        // 
        this.$axios.post('users',this.addForm).then(res=>{
          // console.log(res.data)
          if(res.data.meta.status!=201) return this.$message.error(res.data.meta.msg)
          this.$message.success(res.data.meta.msg);
          // 
          this.addDialogVisible = false;
          // 
          this.getUserList();
        }).catch(err=>{
          console.log(err);
        })
      });
    },
    // 
    showEditDialog(id){
      // console.log(id)
      this.$axios.get('users/' + id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error(res.data.meta.msg);
        }
        this.editForm = res.data.data;
        this.editDialogVisible=true;
      }).catch(err=>{
        console.log(err);
      })
    },
    // 
    editDialogClosed(){
      this.$refs.editFormRef.resetFields();
    },
    // 
    editUserInfo(){
      this.$refs.editFormRef.validate(valid=>{
        if(!valid) return;
        // 
        this.$axios.put('users/'+this.editForm.id,{
          email:this.editForm.email,
          mobile:this.editForm.mobile
        }).then(res=>{
          if(res.data.meta.status !== 200){
            return this.$message.error(res.data.meta.msg);
          }
          // 
          this.editDialogVisible = false;
          // 
          this.getUserList();
          // 
          this.$message.success(res.data.meta.msg)
        }).catch(err=>err)
      })
    },
    // id
    async removeUserById(id){
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      // confirm
      // cancel
      if(confirmResult != 'confirm'){
        return this.$message.info('');
      }
      this.$axios.delete('users/'+id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success(res.data.meta.msg);
        this.getUserList();
      }).catch(err=>err)
    },
    // 
    setRole(userInfo){
      this.userInfo = userInfo;
      // 
      this.$axios.get('roles').then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.rolesList = res.data.data;
        this.setRoleDialogVisible = true;
      }).catch(err=>err)
    },
    // 
    saveRoleInfo(){
      if(!this.selectedRoleId){
        return this.$message.error('');
      }
      this.$axios.put(`users/${this.userInfo.id}/role`,{
        rid:this.selectedRoleId}).then(res=>{
        console.log(res.data)
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success('');
        this.getUserList();
        this.setRoleDialogVisible = false;
      }).catch(err=>err)
    },
    // 
    setRoleDialogClosed(){
      this.selectedRoleId = '';
      this.userInfo = {};
    }
  }
}
</script>
<style lang="less" scoped>
</style>



