<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/image/login.jpg" alt="">
      </div>
      <!--  -->
      <el-form  ref="loginFormRef" :model="loginForm" :rules="loginFormRules" class="login_form">
        <!--  -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-retractwode"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input type="password" v-model="loginForm.password" prefix-icon="iconfont icon-retractmima"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "login_container",
  data(){
    return {
      // 
      loginForm:{
        username:"admin",
        password:"123456"
      },
      // 
      loginFormRules:{
        // 
        username:[
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password:[
        { required: true, message: '', trigger: 'blur' },
        { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  components: {},
  created(){},
  mounted(){},
  methods: {
    // 
    resetLoginForm(){
      this.$refs.loginFormRef.resetFields();
    },
    login(){
      let postData = JSON.stringify(this.loginForm);
      this.$refs.loginFormRef.validate(valid=>{
        if(!valid) return;
        this.$axios({
        method:'post',
        url:'login',
        data:postData,
        headers:{'Content-Type':'application/json'}
        }).then(res=>{
          console.log(res.data.data.token)
          if(res.data.meta.status!=200) return this.$message.error(res.data.meta.msg)
          this.$message.success(res.data.meta.msg);
          // tokensessionStorage
          window.sessionStorage.setItem('token',res.data.data.token);
          // 
          // alert('1')
          this.$router.push('/home');
        }).catch(function(err){
          console.log(err);
          return this.$message.error('')
        });
      })
    }
  }
}
</script>
<style lang="less" scoped>
  @import "../assets/less/Login.less";
</style>



