<template>
  <div>
    <!--   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>

      <!-- table -->
      <el-table :data="goodslist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="" width="100px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="65px"></el-table-column>
        <el-table-column prop="add_time" label="" width="145px">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="125px">
          <!--  -->
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total" background>
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: '',
  data(){
    return {
       // 
      queryInfo:{
        query:'',
        // 
        pagenum:1,
        // 
        pagesize:10
      },
      // 
      goodslist:[],
      total:0,
    }
  },
  components: {},
  created(){
    this.getGoodsList()
  },
  mounted(){},
  methods: {
    // 
    getGoodsList(){
      this.$axios({
        methods:'get',
        url:'goods',
        params:this.queryInfo
      }).then(res=>{
        // console.log(res.data)
        if(res.data.meta.status !== 200)
          return this.$message.error('');
        this.$message.success('')
        this.goodslist = res.data.data.goods;
        this.total = res.data.data.total;
      }).catch(function(err){
        console.log(err);
      })
    },
    // pagesize
    handleSizeChange(newSize){
      this.queryInfo.pagesize=newSize;
      this.getGoodsList();
    },
    // 
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage;
      this.getGoodsList();
    },
    // id
    async removeById(id){
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      // confirm
      // cancel
      if(confirmResult != 'confirm'){
        return this.$message.info('');
      }
      this.$axios.delete('goods/'+id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success('');
        this.getGoodsList();
      }).catch(err=>err)
    },
    // 
    goAddpage(){
      this.$router.push('/goods/add')
    },
  }
}
</script>
<style lang="less" scoped>
</style>



