<template>
  <div class="">
     <!--   -->
     <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="rolelist" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!-- <pre>
              {{scope.row}}
            </pre> -->
            <el-row :class="['bdbottom',i1 === 0 ? 'bdtop' : '','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for -->
                <el-row :class="[i2 === 0 ? '' : 'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" v-for="(item3,i3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column label="" width="300px">
          <!--  -->
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeRoleById(scope.row.id)"></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightDialog(scope.row)"></el-button>
            </el-tooltip>

          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible"
      width="50%" @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addRole"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisible"
      width="50%" @close="editDialogClosed">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px">
          <el-form-item label="" >
            <el-input v-model="editForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="editForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editRoleInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="setRightDialogVisible"
    width="50%" @close="setRightDialogClosed">
      <!--  -->
      <el-tree :data="rightslist" :props="treeProps" show-checkbox node-key="id"
      default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: '',
  data(){
    return {
      // 
      rolelist:[],
      // 
      addDialogVisible:false,
      // 
      addForm:{
        roleName:'',
        roleDesc:''
      },
      // 
      editDialogVisible:false,
      // 
      editForm:{},
      // 
      addFormRules:{
        roleName:[
          {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            min:3,
            max:10,
            message:'3~10',
            trigger:'blur'
          }
        ],
        roleDesc:[
          {
            required:true,
            message:'',
            trigger:'blur'
          }
        ]
      },
      // 
      editFormRules:{
        roleName:[
          {
            required:true,
            message:'',
            trigger:'blur'
          },
          {
            min:3,
            max:10,
            message:'3~10',
            trigger:'blur'
          }
        ],
        roleDesc:[
          {
            required:true,
            message:'',
            trigger:'blur'
          }
        ]
      },
      // 
      setRightDialogVisible:false,
      // 
      rightslist:[],
      // 
      treeProps:{
        label:'authName',
        children:'children'
      },
      // Id
      defKeys:[],
      // id
      roleId:''
    }
  },
  created(){
    this.getRolesList()
  },
  methods: {
    // 
    getRolesList(){
      this.$axios.get('roles').then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.rolelist = res.data.data;
      }).catch(err=>err)
    },
    // 
    addDialogClosed(){
      // 
      this.$refs.addFormRef.resetFields();
    },
    // 
    addRole(){
      this.$refs.addFormRef.validate(valid=>{
        if(!valid) return;
        // 
        this.$axios.post('roles',this.addForm).then(res=>{
          console.log(res.data)
          if(res.data.meta.status!=201) return this.$message.error(res.data.meta.msg)
          this.$message.success(res.data.meta.msg);
          // 
          this.addDialogVisible = false;
          // 
          this.getRolesList();
        }).catch(err=>{
          console.log(err);
        })
      });
    },
    // 
    showEditDialog(id){
      // console.log(id)
      this.$axios.get('roles/' + id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error(res.data.meta.msg);
        }
        this.editForm = res.data.data;
        this.editDialogVisible=true;
      }).catch(err=>{
        console.log(err);
      })
    },
    // 
    editDialogClosed(){
      this.$refs.editFormRef.resetFields();
    },
    // 
    editRoleInfo(){
      this.$refs.editFormRef.validate(valid=>{
        if(!valid) return;
        // 
        this.$axios.put('roles/'+this.editForm.roleId,{
          roleName:this.editForm.roleName,
          roleDesc:this.editForm.roleDesc
        }).then(res=>{
          if(res.data.meta.status !== 200){
            return this.$message.error(res.data.meta.msg);
          }
          // 
          this.editDialogVisible = false;
          // 
          this.getRolesList();
          // 
          this.$message.success(res.data.meta.msg)
        })
      })
    },
    // id
    async removeRoleById(id){
       // 
       const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      // confirm
      // cancel
      if(confirmResult != 'confirm'){
        return this.$message.info('');
      }
      this.$axios.delete('roles/'+id).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success(res.data.meta.msg);
        this.getRolesList();
      }).catch(err=>err)
    },
    // id
    async removeRightById(role,rightId){
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      // confirm
      // cancel
      if(confirmResult != 'confirm'){
        return this.$message.info('');
      }
      this.$axios.delete(`roles/${role.id}/rights/${rightId}`).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success('');
        role.children = res.data.data
      }).catch(err=>err)
    },
    // 
    showSetRightDialog(role){
      this.roleId = role.id;
      // 
      this.$axios.get('rights/tree').then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        // data
        this.rightslist = res.data.data;
        // Id
        this.getLeafKeys(role,this.defKeys)
        this.setRightDialogVisible = true;
      }).catch(err=>err)
    },
    // iddefKeys
    getLeafKeys(node,arr){
      if(!node.children){
        // nodechildren
        return arr.push(node.id);
      }
      node.children.forEach(item=>this.getLeafKeys(item,arr))
    },
    // 
    setRightDialogClosed(){
      this.defKeys = [];
    },
    // 
    allotRights(){
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',');
      this.$axios.post(`roles/${this.roleId}/rights`,
      {rids:idStr}).then(res=>{
        if(res.data.meta.status !== 200){
          return this.$message.error('');
        }
        this.$message.success('');
        this.getRolesList();
        this.setRightDialogVisible = false;
      })
    }
  }
}
</script>
<style lang="less" scoped>
  @import "../../assets/less/Roles.less";
</style>



